<template>
  <div id="points">
    <div id="header">
        <span @click="back()"><i class="el-icon-arrow-left"></i></span>
        <span>我的积分</span>
    </div>
    <div id="bg">
        <div class="incont">
            <div class="leftright">
                <p>当前积分</p>
                <div @click="todetail()" class="wright">
                    <div class="que">?</div>
                    <p class="shuoming">积分说明</p>
                </div>
            </div>
            <span class="wcount" v-text="points"></span>分
            <div @click="show()" class="deposit">
                积分兑换商品
            </div>
        </div>
    </div>
    <div id="fotcont">
          <span>最近30天积分记录</span>
          <div class="hint"> 
              <img :src="imgsrc" alt="">
          </div>
          <p class="wfoot">最近30天无积分记录</p>
          <p class="wfoot fot">快去下单赚取大量积分吧</p>
    </div>
    <div class="animated bounceIn" id="walert" v-show="isshow">
          <div class="tip_icon">
            <span></span>
            <span></span>
          </div>
          <p v-text="warncont"></p>
          <div @click="isshow=!isshow" class="wbutton">
            确定
          </div>
      </div>
    <router-view class="detail"></router-view>
  </div>
</template>

<script>
export default {
    name: 'points',
    data() {
        return {
            warncont:"快去下单赚取大量积分吧",
            isshow:false,
            points:"0",
            imgsrc: "data:image/png;base64,"
        }
    },
    methods: {
        back () {
        this.$router.go(-1);
        },
        show () {
            if (this.points == 0) {
                this.isshow = true;
            }
        },
        todetail () {
            this.$router.push({name: "Detail1"})
        }
    },
}
</script>
<style scoped>
.detail {
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    background: white;
}
#walert {
  background: white;
  border-radius: 0.3rem;
  width: 80vw;
  height: 2rem;
  position: absolute;
  left: 10vw;
  top: 20%;
}
#walert>p {
  width: 100%;
  height: 0.5rem;
  background: white;
  position: absolute;
  bottom: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
}
.wbutton {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
  color: white;
  font-weight: 800;
  background: #4cd964;
  border-bottom-right-radius:0.3rem;
  border-bottom-left-radius:0.3rem;
}
.tip_icon {
  width: 0.8rem;
  height: 0.8rem;
  border: 0.03rem solid #f8cb86;
  border-radius: 50%;
  margin-left: 36%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(1) {
  display: inline-block;
  width: 0.03rem;
  height: 0.34rem;
  background-color: #f8cb86;
  margin-left: 49%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(2) {
  display: inline-block;
  width: 0.05rem;
  height:0.05rem;
  border: 0.01rem;
  border-radius: 50%;
  margin-left: 49%;
  background-color: #f8cb86;
}
.wfoot {
    width: 100%;
    text-align: center;
    font-size: 0.16rem;
    margin-top: 0.1rem;
    color: gray;
}
.hint>img{
    width: 2.5rem;
    margin-left: 0.5rem;
}
#fotcont {
    width: 100%;
}
#fotcont>span {
    height: 0.5rem;
    line-height: 0.5rem;
    margin-left: 0.2rem;
    font-size: 0.13rem;
    color: gray;
}
.deposit {
    width: 90%;
    margin-left: 5%;
    text-align: center;
    height: 0.4rem;
    color: white;
    border-radius: 0.05rem;
    line-height: 0.4rem;
    background: #fe6d47;
    margin-top: 0.3rem;
}
.wcount {
    font-size: 0.4rem;
    font-weight: 400;
    margin-left: 0.2rem;
}
.leftright {
    width: 100%;
    height: 0.3rem;
}
.leftright>p:nth-child(1) {
    color: gray;
    font-size: 0.12rem;
    height: 0.3rem;
    line-height: 0.3rem;
    float: left;
    margin-left: 0.1rem;
}
.wright {
    float: right;
    margin-right: 5vw;
    width: 0.7rem;
    height: 0.3rem;
}
.shuoming {
    color: #3190e8;
    font-size: 0.12rem;
    line-height: 0.3rem;
}
.que {
    float: left;
    width: 0.15rem;
    height: 0.15rem;
    background: #3190e8;
    text-align: center;
    border-radius: 50%;
    font-size: 0.14rem;
    line-height: 0.15rem;
    color: white;
    margin-top: 0.075rem;
}
.incont {
    width: 90vw;
    height:1.6rem;
    background: white;
    margin-left: 5vw;
    margin-top: 0.1rem;
    border-radius: 0.05rem;
    overflow: hidden;
}
#bg {
    height: 1.8rem;
    background: #3190e8;
    overflow: hidden;
}
#wheader {
    height:0.5rem;
    background: #3190e8;
    color: white;
}
#header {
    width: 100%;
    height: 0.5rem;
    background: #3190e8;
    color: white;
}
#header>span:nth-child(1) {
    line-height: 0.5rem;
    margin-left: 5vw;
    font-size: 0.2rem
}
#header>span:nth-child(2) {
  line-height: 0.5rem;
  font-weight: 800;
  margin-left: 30vw;
  font-size: 0.2rem;
}
</style>



