<template>
  <div id="info">
      <div id="header">
          <span @click="back()"><i class="el-icon-arrow-left"></i></span>
          <span>账户信息</span>
      </div>
      <div class="input1">
          <span class="left1">头像</span>
          <div class="right1">
            <img src="//elm.cangdu.org/img/default.jpg" alt="">
            <span class="jiantou"><i class="el-icon-arrow-right"></i></span>
          </div>
      </div>
      <div class="inputk">
        <div>用户名</div>
        <div class="user">
          <p class="name" v-text="username"></p>
        </div>
        <div class="rightk"><i class="el-icon-arrow-right"></i></div>
      </div>
      <div @click="toaddress()" class="input3">
        <span>收货地址</span>
        <span class="right2"><i class="el-icon-arrow-right"></i></span>
      </div>
      <div class="gray"><p>账号绑定</p></div>
      <div class="input4" @click="alert()">
        <img :src="phone" alt="">
        <span>手机</span>
        <span class="right2"><i class="el-icon-arrow-right"></i></span>
      </div>
      <div class="gray"><p>安全设置</p></div>
      <div @click="toforget" class="input2">
        <span>登录密码</span>
        <span>修改</span>
        <span class="right2"><i class="el-icon-arrow-right"></i></span>
      </div>
      <div @click="dlogin()" id="exit">退出登录</div>
      <div class="animated bounceIn" id="walert" v-show="isshow">
          <div class="tip_icon">
          <span></span>
          <span></span>
          </div>
          <p v-text="warncont"></p>
          <div @click="isshow=!isshow" class="wbutton">
            确定
          </div>
      </div>
      <div class="animated bounceIn" id="walert1" v-show="isshow1">
          <div class="tip_icon1">
          <span></span>
          <span></span>
          </div>
          <p v-text="warncont1"></p>
          <div @click="isshow1=!isshow1" class="wbuttonleft">
            再等等
          </div>
          <div @click="suredlogin()" class="wbuttonright">
            退出登录
          </div>
      </div>
      <router-view class="detail" :key="key"></router-view>
  </div>
</template>

<script>
export default {
  name: 'info',
  data() {
      return {
          isshow: false,
          isshow1:false,
          warncont:"请在手机App中设置",
          warncont1:"是否退出登录",
          username:"",
          phone: "data:image/png;base64,",
      }
  },
  created() {
    this.username = sessionStorage.username
  },
  computed:{
	    key(){
	        return this.$route.path + Math.random();
	    }
	},
  methods: {
    back () {
      this.$router.push({name:"Profile"})
    },
    toaddress () {
      this.$router.push({name:'Address'})
    },
    toforget () {
      this.$router.push({name:'Forget'})
    },
    alert () {
      this.isshow = true;
    },
    dlogin() {
      this.isshow1 = true;
    },
    suredlogin() {
      this.$http({ 
        method:"get",
        url:"https://elm.cangdu.org/v2/signout"
      }).then((res) => {
          if (res.data.message) {
            sessionStorage.clear();
            this.$router.push({name:"Profile"})
          }
      })
    }
  },
}
</script>
<style>
  .detail {
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    background: #f2f2f2
  }
  .inputk {
    height: 0.5rem;
    background: white;
    border-bottom: 0.01rem solid #eee;
  }
  .inputk>div {
    float: left;
  }
  .inputk>div:nth-child(1) {
    line-height: 0.5rem;
    margin-left: 0.2rem;
  }
  .inputk>div:nth-child(2) {
    width: 1.5rem;
    line-height: 0.5rem;
    margin-left: 0.5rem;
    color: gray;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
  }
  .inputk>div:nth-child(3) {
    line-height: 0.5rem;
    float: right;
    margin-right: 5vw;
    color: gray;
  }
  #exit {
    width: 90vw;
    margin-left: 5vw;
    margin-top: 0.1rem;
    background: #d8584a;
    height: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    border-radius: 0.05rem;
    font-size: 0.12rem;
    color: white;
  }
  .gray>p {
    height: 0.5rem;
    line-height: 0.5rem;
    font-size: 0.12rem;
    margin-left: 0.2rem;
  }
  .input4 {
    height: 0.5rem;
    background: white;
    border-top: 0.01rem solid #eee;
    border-bottom: 0.01rem solid #eee;
    overflow: hidden;
  }
  .input4>span {
    line-height: 0.5rem;
  }
  .input4>span:nth-child(1) {
    margin-left: 5vw;
  }
  .input4>img {
    margin-left: 0.1rem;
    padding-top: 0.1rem;
  }
  .input3 {
    height: 0.5rem;
    background: white;
    border-bottom: 0.01rem solid #eee;
  }
  .input3>span {
    line-height: 0.5rem;
  }
  .input3>span:nth-child(1) {
    margin-left: 5vw;
  }
  .right2 {
    float: right;
    margin-right: 5vw;
    color: gray;
    position: relative;
  }
  .input2 {
    height: 0.5rem;
    background: white;
    border-bottom: 0.01rem solid #eee;
  }
  .input2>span {
    line-height: 0.5rem;
  }
  .input2>span:nth-child(1) {
    margin-left: 5vw;
  }
  .input2>span:nth-child(2) {
    margin-left: 50vw;
    text-overflow: ellipsis;
    overflow: hidden;
    color: gray;
  }
  .left1 {
    margin-left: 5vw;
    line-height: 0.8rem;
  }
  .input1 {
    background: white;
    height:0.8rem;
    margin-top: 0.1rem;
    border-top: 0.01rem solid #eee;
    border-bottom: 0.01rem solid #eee;
  }
  .right1 {
    position: relative;
    width: 0.7rem;
    height: 100%;
    float: right;
    margin-right: 3vw;
    overflow: hidden;
  }
  .right1>img {
    width: 0.5rem;
    border-radius: 50%;
    margin-top: 0.15rem;
  }
  .jiantou {
    float: right;
    line-height: 0.8rem;
  }
  #header {
    width: 100%;
    height: 0.5rem;
    background: #3190e8;
    color: white;
  }
  #header>span:nth-child(1) {
      line-height: 0.5rem;
      margin-left: 5vw;
      font-size: 0.2rem
  }
  #header>span:nth-child(2) {
    line-height: 0.5rem;
    font-weight: 800;
    margin-left: 30vw;
    font-size: 0.2rem;
  }
  #walert {
  background: white;
  border-radius: 0.3rem;
  width: 80vw;
  height: 2rem;
  position: absolute;
  left: 10vw;
  top: 25%;
}
#walert>p {
  width: 100%;
  height: 0.5rem;
  background: white;
  position: absolute;
  bottom: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
}
.wbutton {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
  color: white;
  font-weight: 800;
  background: #4cd964;
  border-bottom-right-radius:0.3rem;
  border-bottom-left-radius:0.3rem;
}
.tip_icon {
  width: 0.8rem;
  height: 0.8rem;
  border: 0.03rem solid #f8cb86;
  border-radius: 50%;
  margin-left: 36%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(1) {
  display: inline-block;
  width: 0.03rem;
  height: 0.34rem;
  background-color: #f8cb86;
  margin-left: 49%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(2) {
  display: inline-block;
  width: 0.05rem;
  height:0.05rem;
  border: 0.01rem;
  border-radius: 50%;
  margin-left: 49%;
  background-color: #f8cb86;
}
/* 退出登录窗口 */
  #walert1 {
  background: white;
  border-radius: 0.3rem;
  width: 95vw;
  height: 3rem;
  position: absolute;
  left: 2vw;
  top: 25%;
}
#walert1>p {
  width: 100%;
  height: 0.5rem;
  background: white;
  color: #575757;
  font-size: 0.3rem;
  position: absolute;
  bottom: 1rem;
  text-align: center;
  line-height: 0.5rem;
}
.wbuttonright {
  position: absolute;
  bottom: 0.2rem;
  right: 0.5rem;
  width: 30%;
  height: 0.3rem;
  text-align: center;
  line-height: 0.3rem;
  color: white;
  font-weight: 800;
  background: #dd6b55;
  border-radius:0.1rem;
  font-weight: normal;
}
.wbuttonleft {
  position: absolute;
  bottom: 0.2rem;
  left: 0.5rem;
  width: 30%;
  height: 0.3rem;
  text-align: center;
  line-height: 0.3rem;
  color: white;
  font-weight: 800;
  background: #c1c1c1;
  border-radius:0.1rem;
  font-weight: normal;
}
.tip_icon1 {
  width: 0.8rem;
  height: 0.8rem;
  border: 0.03rem solid #f8cb86;
  border-radius: 50%;
  margin-left: 36%;
  margin-top: 0.1rem;
}
.tip_icon1 span:nth-child(1) {
  display: inline-block;
  width: 0.03rem;
  height: 0.34rem;
  background-color: #f8cb86;
  margin-left: 49%;
  margin-top: 0.1rem;
}
.tip_icon1 span:nth-child(2) {
  display: inline-block;
  width: 0.05rem;
  height:0.05rem;
  border: 0.01rem;
  border-radius: 50%;
  margin-left: 49%;
  background-color: #f8cb86;
}
</style>



