<template>
  <div id="commend">
    <div id="header">
        <span @click="back()"><i class="el-icon-arrow-left
"></i></span>
        <span>推荐有奖</span>
    </div>
    <div id="activebanner">
        <img :src="img1" alt="">
    </div>
    <div id="choose">
       <div @click="yaoqing()">
           <img :src="weixin" alt="">
           <p>邀请微信好友</p>
        </div>
       <div @click="yaoqing()"><img :src="qq" alt=""><p>邀请QQ好友</p></div>
       <div @click="yaoqing()"><img :src="facetoface" alt=""><p>面对面邀请</p></div>
    </div>
    <div id="footcont">
        <div class="wleft">
            <p>累计收益</p>
            <p class="redtxt">0元</p>
        </div>
        <div class="wright">
            <p>成功邀请</p>
            <p>0人</p>
        </div>
        <div class="mingxi">-收益明细-</div>
        <div class="hongbaoimg">
            <img :src="hongbao" alt="">
        </div>
        <p>还不赶紧去邀请好友</p>
    </div>
    <div class="animated bounceIn" id="walert" v-show="isshow">
        <div class="tip_icon">
        <span></span>
        <span></span>
        </div>
        <p v-text="warncont"></p>
        <div @click="isshow=!isshow" class="wbutton">
        确定
        </div>
    </div>
  </div>
</template>

<script>
let img1 = require("../../../../static/img/activity.png");
export default {
    name: 'commend',
    data() {
        return {
            img1,
            warncont:"请在饿了么APP中打开",
            isshow:false,
            weixin:"data:image/png;base64,",
            qq:"data:image/png;base64,",
            facetoface:"data:image/png;base64,",
            hongbao:"data:image/png;base64,"
        }
    },
    methods: {
        back () {
            this.$router.go(-1);
        },
        yaoqing () {
            this.isshow = true;
        }
    },
}
</script>
<style scoped>
#activebanner>img{
    width: 100%;
}
.hongbaoimg {
    width: 100%;
    float: left;
    margin-top: 0.4rem;
}
.hongbaoimg>img {
    width: 0.3rem;
}
#footcont {
    margin-top: 0.2rem;
    text-align: center;
    overflow: hidden;
}
#footcont p {
    color: gray;
    font-size: 0.16rem;
}
.mingxi {
    float: left;
    width: 100%;
    color: gray;
    margin-top: 0.3rem;
}
.wleft {
    float: left;
    width: 49%;
    border-right: 0.01rem solid gray;
    text-align: center;
}
.wright {
    float:left;
    text-align: center;
    width: 50%
}
#choose {
    width: 100%;
    height: 1.3rem;
    background: white;
}
#choose>div {
    float: left;
    width: 33.3%;
    text-align: center;
}
#choose img {
    width: 0.6rem;
    margin-top: 0.2rem;
}
#choose p {
    font-size: 0.13rem;
    margin-top: 0.15rem;
} 
#commend {
    background:#f5f5f5;
    overflow: hidden; 
}
#header {
    width: 100%;
    height: 0.5rem;
    background: #3190e8;
    color: white;
}
#header>span:nth-child(1) {
    line-height: 0.5rem;
    margin-left: 5vw;
    font-size: 0.2rem
}
#header>span:nth-child(2) {
  line-height: 0.5rem;
  font-weight: 800;
  margin-left: 30vw;
  font-size: 0.2rem;
}
#walert {
  background: white;
  border-radius: 0.3rem;
  width: 80vw;
  height: 2rem;
  position: absolute;
  left: 10vw;
  top: 25%;
}
#walert>p {
  width: 100%;
  height: 0.5rem;
  background: white;
  position: absolute;
  bottom: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
}
.wbutton {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
  color: white;
  font-weight: 800;
  background: #4cd964;
  border-bottom-right-radius:0.3rem;
  border-bottom-left-radius:0.3rem;
}
.tip_icon {
  width: 0.8rem;
  height: 0.8rem;
  border: 0.03rem solid #f8cb86;
  border-radius: 50%;
  margin-left: 36%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(1) {
  display: inline-block;
  width: 0.03rem;
  height: 0.34rem;
  background-color: #f8cb86;
  margin-left: 49%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(2) {
  display: inline-block;
  width: 0.05rem;
  height:0.05rem;
  border: 0.01rem;
  border-radius: 50%;
  margin-left: 49%;
  background-color: #f8cb86;
}
</style>



