<template>
  <div id="balance">
      <div id="wheader">
          <span @click="back()"><i class="el-icon-arrow-left"></i></span>
          <span>我的余额</span>
      </div>
      <div id="bg">
        <div class="incont">
            <div class="leftright">
                <p>当前余额</p>
                <div @click="todetail()" class="wright">
                    <div class="que">?</div>
                    <p class="shuoming">余额说明</p>
                </div>
            </div>
            <span class="wcount" v-text="countmoney"></span>元
            <div class="deposit">
                提现
            </div>
        </div>
      </div>
      <div id="fotcont">
          <span>交易明细</span>
          <div class="hint"> 
              <img :src="imgsrc" alt="">
          </div>
          <p class="wfoot">暂无明细记录</p>
      </div>
      <router-view class="detail"></router-view>
  </div>
</template>

<script>
export default {
    name: 'balance',
    data() {
        return {
            countmoney: '0.00',
            imgsrc: "data:image/png;base64,"
        }
    },
    methods: {
        back () {
            this.$router.go(-1);
        },
        todetail () {
            this.$router.push({name: 'Detail'})
        }
    },
}
</script>
<style scoped>
.detail {
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    background:white;
}
.wfoot {
    width: 100%;
    text-align: center;
    font-size: 0.16rem;
    margin-top: 0.1rem;
    color: gray;
}
.hint>img{
    width: 2.5rem;
    margin-left: 0.5rem;
}
#fotcont {
    width: 100%;
}
#fotcont>span {
    height: 0.5rem;
    line-height: 0.5rem;
    margin-left: 0.2rem;
    font-size: 0.13rem;
    color: gray;
}
.deposit {
    width: 90%;
    margin-left: 5%;
    text-align: center;
    height: 0.4rem;
    color: white;
    border-radius: 0.05rem;
    line-height: 0.4rem;
    background: #ccc;
    margin-top: 0.3rem;
}
.wcount {
    font-size: 0.4rem;
    font-weight: 400;
    margin-left: 0.2rem;
}
.leftright {
    width: 100%;
    height: 0.3rem;
}
.leftright>p:nth-child(1) {
    color: gray;
    font-size: 0.12rem;
    height: 0.3rem;
    line-height: 0.3rem;
    float: left;
    margin-left: 0.1rem;
}
.wright {
    float: right;
    margin-right: 5vw;
    width: 0.7rem;
    height: 0.3rem;
}
.shuoming {
    color: #3190e8;
    font-size: 0.12rem;
    line-height: 0.3rem;
}
.que {
    float: left;
    width: 0.15rem;
    height: 0.15rem;
    background: #3190e8;
    text-align: center;
    border-radius: 50%;
    font-size: 0.14rem;
    line-height: 0.15rem;
    color: white;
    margin-top: 0.075rem;
}
.incont {
    width: 90vw;
    height:1.6rem;
    background: white;
    margin-left: 5vw;
    margin-top: 0.1rem;
    border-radius: 0.05rem;
    overflow: hidden;
}
#bg {
    height: 1.8rem;
    background: #3190e8;
    overflow: hidden;
}
#wheader {
    height:0.5rem;
    background: #3190e8;
    color: white;
}
#wheader>span:nth-child(1) {
      line-height: 0.5rem;
      margin-left: 5vw;
      font-size: 0.2rem
}
#wheader>span:nth-child(2) {
    line-height: 0.5rem;
    font-weight: 800;
    margin-left: 30vw;
    font-size: 0.2rem;
  }
</style>



