<template>
  <div id="voucher">
      <div id="shuoming">
        <img :src="qus" alt="">
        <span @click="tovoucherdes()">商家代金券说明</span>
      </div>
      <div id="cont">
        <div class="chit">
          <img :src="img1" alt="">
          <div>无法使用代金券</div>
          <p>非客户端或客户端版本过低</p>
        </div>
      </div>
      <div @click="todownload()" id="download">
        下载或升级客户端
      </div>
      <router-view class="detail"></router-view>
  </div>
</template>

<script>
export default {
  name: 'voucher',
  data() {
      return {
        qus: "data:image/png;base64,",
        img1:"data:image/png;base64,",

      }
  },
  methods: {
      todownload () {
        this.$router.push({name: "Download"})
      },
      tovoucherdes () {
        this.$router.push({name: "Voucherdes"})
      }
  },
}
</script>
<style scoped>
.detail {
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    background:white;
}
#cont {
  width: 100%;
}
.chit {
  height: 3rem;
  text-align: center;
}
.chit>img {
  width: 1.5rem;
  margin-top: 1.3rem;
}
.chit>div {
  margin-top: 0.2rem;
  color: gray;
}
.chit>p {
  color: gray;
  font-size: 0.16rem;
  margin-top: 0.2rem;
}
#shuoming {
  width: 100%;
  height: 0.2rem;
  margin-right: 0.2rem;
  margin-top: 0.1rem;
}
#shuoming>img{
  float: left;
  width: 0.15rem;
  margin-left: 65vw;
}
#shuoming>span {
  float: left;;
  font-size: 0.12rem;
  color: #3190e8;
}
#download {
  width: 1.5rem;
  text-align: center;
  line-height: 0.4rem;
  margin-left: 1.2rem;
  border-radius: 0.05rem;
  height: 0.4rem;
  background: #56d176;
  color: white;
}
</style>



