      </div>
    </div>
    <router-view class="detail"></router-view>
  </div>
</template>
<script>
export default {
    data() {
        return {
            name:"",
            shengimg: "data:image/jpeg;base64,",
            baoimg: "data:image/jpeg;base64,",
        }
    },
    created() {
      this.name = sessionStorage.username;
    },
    methods: {
      tousercart() {
        this.$router.push({name: "Usercart"})
      },
      tovipdescription () {
        this.$router.push({name: "Vipdescription"})
      },
      topayment () {
        this.$router.push({name: "Payment"})
      }
    },
};
</script>
