<template>
    <div id="payment">
        <div id="header">
            <span @click="back()">＜</span>
            <span>在线支付</span>
        </div>
        <div id="pay">
            <p>支付剩余时间</p>
            <p class="countdown">
                <span>00:</span>
                <span>{{min}}:</span>
                <span>{{seconds}}</span>
            </p>
        </div>
        <div id="choose">选择支付方式</div>
        <div class="zhifubao">
            <img class="logo" :src="img1" alt="">
            <p class="txt">支付宝</p>
            <i class="el-icon-circle-check-outline"></i>
        </div>
        <div class="weixin">
            <img class="logo" :src="img2" alt="">
            <p class="txt">微信</p>
            <i class="el-icon-circle-check-outline"></i>
        </div>
        <div id="sure">
            确认支付
        </div>
        <div class="animated bounceIn" id="walert" v-show="isshow">
            <div class="tip_icon">
            <span></span>
            <span></span>
            </div>
            <p v-text="warncont"></p>
            <div @click="isshow=!isshow" class="wbutton">
            确定
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'payment',
  data() {
      return {
          isshow:true,
          warncont: "暂不支持支付功能",
          time:900,
          min:"",
          seconds: "",
          img1:"data:image/png;base64,",
          img2: "data:image/png;base64,"
      }
  },
  created() {
      this.countdown();
  },
  methods: {
    back () {
      this.$router.go(-1);
    },
    countdown () {
        var Time1 = setInterval(()=>{
            this.time -= 1;
            this.min = parseInt(this.time / 60);
            this.seconds = (this.time % 60);
        },1000)
        if (this.time == 0) {
            clearInterval(Time1);
        }
    }
  }
}
</script>
<style scoped>
#sure {
    width: 90vw;
    height: 0.5rem;
    background: #4cd964;
    color: white;
    text-align: center;
    line-height: 0.5rem;
    margin-left: 5vw;
    margin-top: 0.2rem;
    border-radius: 0.05rem;
}
.zhifubao {
    background: white;
    border: 0.01rem solid #eee;
    height: 0.8rem;
}
.zhifubao>i {
    float:right;
    margin-top: 0.25rem;
    margin-right: 0.3rem;
    font-size: 0.3rem;
    border-radius: 55%;
    /* background: #4cd964; */
}
.logo {
    float: left;
    width: 0.5rem;
    margin-top: 0.15rem;
    margin-left: 0.2rem;
}
.txt {
    float: left;
    margin-top: 0.3rem;
    color: gray;
}
.weixin {
    background: white;
    height: 0.8rem;
}
.weixin>i {
    float:right;
    margin-top: 0.25rem;
    margin-right: 0.3rem;
    font-size: 0.3rem;
}
#choose {
    height: 0.4rem;
    color: gray;
    line-height: 0.4rem;
    margin-left: 0.2rem;
}
#payment {
    background: #f5f5f5;
}
.countdown {
    margin-top: 0.2rem;
}
.countdown>span {
    font-size: 0.4rem;
}
#pay {
    text-align: center;
    background: white;
    overflow: hidden;
    height: 2rem;
}
#pay>p:nth-child(1) {
    color: gray;
    font-size: 0.16rem;
    margin-top: 0.6rem;
    text-align: center;
}
#header {
    width: 100%;
    height: 0.5rem;
    background: #3190e8;
    color: white;
}
#header>span:nth-child(1) {
    line-height: 0.5rem;
    margin-left: 5vw;
    font-size: 0.2rem
}
#header>span:nth-child(2) {
  line-height: 0.5rem;
  font-weight: 800;
  margin-left: 30vw;
  font-size: 0.2rem;
}
#walert {
  background: white;
  border-radius: 0.3rem;
  width: 80vw;
  height: 2rem;
  position: absolute;
  left: 10vw;
  top: 25%;
}
#walert>p {
  width: 100%;
  height: 0.5rem;
  background: white;
  position: absolute;
  bottom: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
}
.wbutton {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
  color: white;
  font-weight: 800;
  background: #4cd964;
  border-bottom-right-radius:0.3rem;
  border-bottom-left-radius:0.3rem;
}
.tip_icon {
  width: 0.8rem;
  height: 0.8rem;
  border: 0.03rem solid #f8cb86;
  border-radius: 50%;
  margin-left: 36%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(1) {
  display: inline-block;
  width: 0.03rem;
  height: 0.34rem;
  background-color: #f8cb86;
  margin-left: 49%;
  margin-top: 0.1rem;
}
.tip_icon span:nth-child(2) {
  display: inline-block;
  width: 0.05rem;
  height:0.05rem;
  border: 0.01rem;
  border-radius: 50%;
  margin-left: 49%;
  background-color: #f8cb86;
}
</style>



